/*~
  name: Switch
  category: Switch
  elements: ons-switch
  markup: |
    <label class="switch">
      <input type="checkbox" class="switch__input">
      <div class="switch__toggle">
        <div class="switch__handle"></div>
      </div>
    </label>
    <label class="switch">
      <input type="checkbox" class="switch__input" checked>
      <div class="switch__toggle">
        <div class="switch__handle"></div>
      </div>
    </label>
    <label class="switch">
      <input type="checkbox" class="switch__input" disabled>
      <div class="switch__toggle">
        <div class="switch__handle"></div>
      </div>
    </label>
*/
.switch {
  display: inline-block;
  vertical-align: top;

  /* mixin: reset-box-model */
  box-sizing: border-box;
  background-clip: padding-box;

  /* mixin: hide-input-parent */
  position: relative;

  min-width: 51px;
  font-size: 17px;
  font-size: 17px;
  font-size: var(--font-size);
  padding: 0 20px;
  border: none;
  overflow: visible;
  width: 51px;
  width: 51px;
  width: var(--switch-width);
  height: 32px;
  height: 32px;
  height: var(--switch-height);
  z-index: 0;
  text-align: left;
}
.switch__input {
  /* mixin: hide-input */
  position: absolute;
  right: 0;
  top: 0;
  left: 0;
  bottom: 0;
  padding: 0;
  border: 0;
  background-color: transparent;
  vertical-align: top;
  outline: none;
  width: 100%;
  height: 100%;
  margin: 0;
  -webkit-appearance: none;
  appearance: none;

  z-index: 0;
}
/* switch toggle background */
.switch__toggle {
  background-color: white;
  background-color: white;
  background-color: var(--switch-background-color);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 30px;
  transition-property: all;
  transition-duration: 0.35s;
  transition-timing-function: ease-out;
  box-shadow: inset 0 0 0 2px #e5e5e5;
  box-shadow: inset 0 0 0 2px #e5e5e5;
  box-shadow: inset 0 0 0 2px var(--switch-border-color);
}
/* switch toggle circle */
.switch__handle {
  /* mixin: reset-box-model */
  box-sizing: border-box;
  background-clip: padding-box;

  position: absolute;
  content: "";
  border-radius: 28px;
  height: 28px;
  width: 28px;
  background-color: white;
  background-color: white;
  background-color: var(--switch-thumb-background-color);
  left: 1px;
  top: 2px;
  transition-property: all;
  transition-duration: 0.35s;
  transition-timing-function: cubic-bezier(0.59, 0.01, 0.5, 0.99);
  box-shadow: 0 0 1px 0 rgba(0, 0, 0, 0.25), 0 3px 2px rgba(0, 0, 0, 0.25);
}
.switch--active__handle {
  transition: none;
}
:checked + .switch__toggle {
  box-shadow: inset 0 0 0 2px #44db5e;
  box-shadow: inset 0 0 0 2px #44db5e;
  box-shadow: inset 0 0 0 2px var(--switch-checked-background-color);
  background-color: #44db5e;
  background-color: #44db5e;
  background-color: var(--switch-checked-background-color);
}
:checked + .switch__toggle > .switch__handle {
  left: 21px;
  box-shadow: 0 3px 2px rgba(0, 0, 0, 0.25);
}
:disabled + .switch__toggle {
  /* mixin: disabled */
  opacity: 0.3;
  cursor: default;
  pointer-events: none;
}
.switch__touch {
  position: absolute;
  top: -5px;
  bottom: -5px;
  left: -10px;
  right: -10px;
}
